
.popover-container {
  position: relative;
}
.anchor {
  position: absolute;
  -webkit-transform-origin: top center;
          transform-origin: top center;
  z-index: 100;
}
.anchor.direction-top {
    bottom: 100%;
}
.anchor.direction-bottom {
    top: 100%;
}
.anchor.direction-left {
    top: 0;
    right: 100%;
}
.anchor.direction-right {
    top: 0;
    left: 100%;
}
.anchor.direction-left.align-top, .anchor.direction-right.align-top {
    top: 0;
}
.anchor.direction-left.align-middle, .anchor.direction-right.align-middle {
    top: 50%;
}
.anchor.direction-left.align-bottom, .anchor.direction-right.align-bottom {
    top: initial;
    bottom: 0;
}
.anchor.direction-top.align-left, .anchor.direction-bottom.align-left {
    left: 0;
}
.anchor.direction-top.align-center, .anchor.direction-bottom.align-center {
    left: 50%;
}
.anchor.direction-top.align-right, .anchor.direction-bottom.align-right {
    right: 0;
}
.anchor .content-container .content.direction-top.align-center, .anchor .content-container .content.direction-bottom.align-center {
    margin-left: -50%;
}
.anchor .content-container .content.direction-left.align-middle, .anchor .content-container .content.direction-right.align-middle {
    margin-top: -50%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.fade-enter, .fade-leave-to {
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  opacity: 0;
}



